import React, {PureComponent} from 'react';
import Label from 'r-cmui/components/Label';
import Table from 'r-cmui/components/Table';
import Row from 'r-cmui/components/Row';
import Col from 'r-cmui/components/Col';

import Demo1 from './demo1';
import Demo2 from './demo2';


class Page extends PureComponent {
    render () {
        return (
            <div className='main-container'>
                <h1 className='page-h1'>Contacts </h1>
                <blockquote className='page-tip'>
                <br/>
                </blockquote>

                <h1 className='page-h1'></h1>

                <Row>
                    <Col grid={0.5} className='code-col'>
                        <Label className='code-box'>
                            <Demo1></Demo1>
                        </Label>
                        {/* <Label className='code-box'>
                            <Demo3></Demo3>
                        </Label> */}
                    </Col>
                    <Col grid={0.5} className='code-col'>
                        <Label className='code-box'>
                            <Demo2></Demo2>
                        </Label>
                        {/* <Label className='code-box'>
                            <Demo4/>
                        </Label> */}
                    </Col>
                </Row>

                <h2 className='page-h2'>API</h2>
                <h3 className='page-h3'>Contacts</h3>

                <Table columns={[
                    {name: 'param', text: ''},
                    {name: 'desc', text: ''},
                    {name: 'type', text: ''},
                    {name: 'default', text: ''}
                ]} bordered={true} data={[
                    {param: 'className', desc: 'class', type: 'string', default: ''},
                    {param: 'style', desc: '', type: 'object', default: ''},
                    {param: 'data', desc: '', type: 'object', default: ''},
                    {param: 'selectable', desc: '', type: 'bool', default: ''}
                ]}></Table>

                <h3 className='page-h3'>Methods</h3>

                <ul className='code-methods'>
                    <li>
                        <i>setData(data)</i>
                        <span></span>
                        <ul>
                            <li>
                                data {'Map'} {'{\'A\': [\'xxx\',\'xxx\']}'}
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>addItem(char, item)</i>
                        <span></span>
                        <ul>
                            <li>
                                char {'string'} ABCD...
                            </li>
                            <li>
                                item {'object'} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>removeItem(char, item)</i>
                        <span></span>
                        <ul>
                            <li>
                                char {'string'} ABCD...
                            </li>
                            <li>
                                item {'object'} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>updateItem(char, oldItem, newItem)</i>
                        <span></span>
                        <ul>
                            <li>
                                char {'string'} ABCD...
                            </li>
                            <li>
                                oldItem {'object'} 
                            </li>
                            <li>
                                newItem {'object'} 
                            </li>
                        </ul>
                    </li>
                </ul>
            </div>
        );
    }
}

export default Page;



